@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

[x-cloak] {
  display: none;
}

h1,h2,h3,h4,h5,h6 {
  font-weight: 700;
}
$headings: (
  1:2,2:1.8,3:1.5,4:1.25,5:1,6:0.8
);
@each $i, $size in $headings {
  h#{$i} {
    font-size: #{$size}em;
    margin: #{$size / 3}em 0;
  }
}

.w-4 {
  width: 1rem;
}
.h-4 {
  height: 1rem;
}

.hero, .hero-services {
  background-position: right center;
  background-size: cover;
}
.no-webp .hero {
  background-image: url('/assets/images/pexels-cedric-fauntleroy-4269694.jpg');
}
.webp .hero {
  background-image: url('/assets/images/pexels-cedric-fauntleroy-4269694.webp');
}
.no-webp .hero-services {
  background-image: url('/assets/images/pexels-andrea-piacquadio-3952125.jpg');
}
.webp .hero-services {
  background-image: url('/assets/images/pexels-andrea-piacquadio-3952125.webp');
}

