.testimonials {
  background-image: url('/assets/images/people_bg.jpg');
}

p {
  margin: 0.75em 0;
}

.text-shadow {
  text-shadow: 1px 1px 1px rgba(0,0,0,0.5);
}

header .wrapper {
  min-height: 42px;
}

@media only screen and (max-width: 767px) {
  header .wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 40px;
    grid-template-areas:
      "logo phone toggle"
      "navbar navbar navbar";
    grid-template-rows: 40px auto;

    .logo {
      grid-area: logo;
    }
    .phone {
      grid-area: phone;
    }
    nav {
      grid-area: navbar;
    }
    .toggle {
      grid-area: toggle;
    }
  }
}